.blog-post-title {
  color: #d9a23b;
  text-decoration: none;
  text-align: center;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(195, 195, 195, 0.3)), to(rgba(95, 95, 95, 0)));
  background: -webkit-linear-gradient(top, rgba(195, 195, 195, 0.3), rgba(95, 95, 95, 0));
  background: -o-linear-gradient(top, rgba(195, 195, 195, 0.3), rgba(95, 95, 95, 0));
  background: linear-gradient(to bottom, rgba(195, 195, 195, 0.3), rgba(95, 95, 95, 0));
  border-radius: 9px;
  border-bottom: 2px solid lightgrey; }

.post-author {
  display: grid;
  float: left;
  background: #dfe5ea;
  color: black;
  font-family: "Dosis", sans-serif;
  padding: .5rem;
  margin: 0 20px;
  grid-template-areas: "avatar name" "avatar summary" "avatar date";
  grid-column-gap: 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-left: solid 4px #6385A9;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; }
  .post-author .author-avatar {
    grid-area: avatar;
    width: 50px;
    height: 50px;
    overflow: hidden;
    border-radius: 100%;
    -webkit-transition: all 2s;
    -o-transition: all 2s;
    transition: all 2s; }
    .post-author .author-avatar:hover {
      border-radius: 0; }
  .post-author .author-name {
    grid-area: name; }
  .post-author .author-summary {
    grid-area: summary; }
  .post-author .post-date {
    grid-area: date;
    color: black;
    font-size: 10px;
    text-align: right; }

.read-more {
  text-decoration: none;
  text-transform: uppercase;
  font-family: "Dosis", sans-serif;
  font-weight: 800;
  font-size: 12px;
  color: #d9a23b; }

pre {
  overflow-x: auto; }

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }
@-webkit-keyframes slideIn {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0; }
  100% {
    opacity: 1; } }
@keyframes slideIn {
  0% {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 0; }
  100% {
    opacity: 1; } }



